import React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title
  const posts = data.allMarkdownRemark.edges

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="About me" />
      <Bio />
      <div className="flex mb-4 text-lg font-bold py-6">
        <div className="w-2/4">
          <h1 className="pt-2">Latest Articles</h1>
        </div>
        <div className="w-2/4 justify-left">
          <button class="bg-gray-300 justify-right font-hairline ml-10 text-xs hover:bg-gray-400 text-gray-800 py-1 px-2 rounded">
            <Link to="/articles"> View All</Link>
          </button>
        </div>
      </div>
      {posts.map(({ node }) => {
        const title = node.frontmatter.title || node.fields.slug
        return (
          <Link style={{ boxShadow: `none` }} to={node.fields.slug}>
            <article
              className="text-xs border-b-2 border-gray-200 hover:bg-blue-100	p-2"
              key={node.fields.slug}
            >
              <header>
                <h3
                  style={{
                    marginBottom: rhythm(1 / 4),
                  }}
                  className="font-semibold"
                >
                  {title}
                </h3>
                <small className="text-xs  text-gray-600">
                  {node.frontmatter.date}
                </small>
              </header>
              <section className="text-xs">
                <p
                  dangerouslySetInnerHTML={{
                    __html: node.frontmatter.description || node.excerpt,
                  }}
                />
              </section>
            </article>
          </Link>
        )
      })}
    </Layout>
  )
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`
